img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
*,
*::after,
*::before {
  box-sizing: border-box;
}
@font-face {
  font-family: "ptsans";
  src: local("ptsans"), local("ptsans Regular"), url("../../fonts/ptsans.woff2") format("woff2"), url("../../fonts/ptsans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "ptsans";
  src: local("ptsans"), local("ptsans bold"), url("../../fonts/ptsansbold.woff2") format("woff2"), url("../../fonts/ptsansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.container {
  padding: 0 20px;
}
@media (min-width: 768px) {
  .container {
    padding-left: 32px;
    padding-right: 31px;
  }
}
.page {
  height: 100%;
  font-family: "ptsans", "Arial", sans-serif;
  font-weight: 400;
}
.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-height: 100%;
  margin: 0;
}
.page-footer {
  height: 300px;
  background-color: tomato;
}
.main-nav {
  position: relative;
}
.main-nav__toggle {
  position: absolute;
  top: 16px;
  right: 18px;
  width: 23px;
  height: 22px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 23px;
  height: 4px;
  background-color: #81B3D2;
  box-shadow: 0 9px 0 0 #81B3D2, 0 18px 0 0 #81B3D2;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 10px;
  left: -1px;
  width: 26px;
  height: 4px;
  background-color: #FFFFFF;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    flex-wrap: wrap;
  }
}
.main-nav__item {
  border-top: 1px solid #9ac2db;
  border-bottom: 1px solid #678fa8;
}
.main-nav__item:first-child {
  border-top: none;
}
.main-nav__item:nth-child(4) {
  border-bottom: none;
}
.main-nav__item:nth-last-child(2) {
  position: relative;
}
.main-nav__item:nth-last-child(2)::after {
  content: "";
  position: absolute;
  bottom: 0;
  display: block;
  width: 100%;
  height: 20px;
  background: linear-gradient(to top, #678fa8 0, transparent 10px);
}
.main-nav--closed .main-nav__item {
  display: none;
}
.main-nav__item--logo {
  height: 56px;
  border: none;
}
.main-nav--closed .main-nav__item--logo {
  display: block;
}
.main-nave__link {
  display: block;
  padding: 19px 15px 12px;
  background-color: #81B3D2;
  font-weight: 700;
  font-size: 18px;
  text-transform: uppercase;
  text-align: center;
  color: #FFFFFF;
}
.main-nav__item--logo {
  text-align: center;
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
